





















import BooleanToggle from "./BooleanToggle.vue";
import StringInput from "./StringInput.vue";
import {Component, Vue, Prop} from "vue-property-decorator";
import OfficeIcon from "../office-ui-fabric-vue/components/Icon/OfficeIcon.vue";
import OfficeTextField from "../office-ui-fabric-vue/components/TextField/OfficeTextField.vue";
import OfficeToggle from "../office-ui-fabric-vue/components/Toggle/OfficeToggle.vue";
import OfficeLabel from "../office-ui-fabric-vue/components/Label/OfficeLabel.vue";

interface IItemOption {
    type: string;
    value?: any;
    options?: any;
}

export interface IItemOptions {
    [key: string]: IItemOption;
}

export interface IAvailableOptions {
    type: string;
    name: string;
    value?: any;
    options?: any;
}

@Component({
    components: {
        OfficeToggle,
        OfficeIcon,
        OfficeTextField,
        OfficeLabel,
        BooleanToggle,
        StringInput
    }
})
export default class OverviewItem extends Vue {
    @Prop({type: String, default: ""}) private title!: string;
    @Prop({type: Object, default: null}) private options?: any;
    @Prop({type: Object, default: null}) private contentStyle?: any;

    private availableOptions: IAvailableOptions[] = this.options
        ? Object.keys(this.options).map((key) => this.mapOption(key, this.options[key])) as any
        : [];

    private mapOption(key: string, option: IItemOption): IAvailableOptions {
        return {
            name: key,
            value: option.value,
            type: option.type,
            options: option.options
        };
    }

    private get currentProps() {
        const result: { [key: string]: any } = {};

        this.availableOptions
            .forEach((s) => result[s.name] = s.value);

        return result;
    }
}
